export default {
	data() {
		return {
			staffList: null
		};
	},
	onLoad(options) {
		this.orderId = options.orderId
		this.GetStaffList()
	},
	methods:{
		viewHeadImg(img){
			uni.previewImage({
				urls: [img]
			})
		},
		viewImg(iindex, imgIndex){
			uni.previewImage({
				urls: this.staffList[iindex].imgs,
				current: imgIndex
			})
		},
		changeStaff(index){
			var that = this
			uni.showModal({
				content: '确定更换技师吗？',
				success(res) {
					if(res.confirm){
						uni.showLoading({
							title: '请稍等'
						})
						uni.request({
							header: {
								token: that.Token
							},
							url: that.Server + 'order/replaceStaff',
							data: {
								orderId: that.orderId,
								staffId: that.staffList[index].id
							},
							success(res) {
								uni.hideLoading()
								if(res.data.code == 0){
									uni.showToast({
										title: '技师更换成功',
										duration: 1500
									})
									setTimeout(() => {
										uni.navigateBack({
											
										})
									}, 1500)
								} else if (res.data.code == 101) {
									uni.showToast({
										icon: "none",
										title: '登录超时，将重新授权登录',
										duration: 1000
									})
									setTimeout(() => {
										uni.reLaunch({
											url: '../login/login'
										})
									}, 2000)
								} else {
									uni.showToast({
										icon: "none",
										title: res.data.msg,
										duration: 1000
									})
								}
								uni.hideLoading()
							}
						})
					}
				}
			})
			
		},
		GetStaffList(){
			var that = this
			uni.showLoading({
				
			})
			uni.request({
				header: {
					token: that.Token
				},
				url: that.Server + 'staff/list',
				data: {
					merchantId: that.MerchantId
				},
				success(res) {
					uni.hideLoading()
					if(res.data.code == 0){
						for(var i in res.data.data){
							res.data.data[i].imgs = JSON.parse(res.data.data[i].imgs)
						}
						
						that.staffList = res.data.data
					} else if (res.data.code == 101) {
						uni.showToast({
							icon: "none",
							title: '登录超时，将重新授权登录',
							duration: 1000
						})
						setTimeout(() => {
							uni.reLaunch({
								url: '../login/login'
							})
						}, 2000)
					} else {
						uni.showToast({
							icon: "none",
							title: res.data.msg,
							duration: 1000
						})
					}
					uni.hideLoading()
				}
			})
		},
	}
}
